<template>
    <div class="m-joke-item">
        <div class="u-content" @click="handleContent" :class="mode === 'single' ? 'on' : ''">
            <i class="u-star" :class="joke.star ? 'on' : ''" v-if="joke.star" title="ç²¾é€‰">â˜…</i>
            <span class="u-sentence" v-html="parse(joke.content)"></span>
        </div>
        <div class="u-misc">
            <div class="u-op">
                <span class="u-user">
                    <img width="24" height="24" :src="user_avatar | showAvatar" />
                    <a
                        :href="joke.user_id | authorLink"
                        target="_blank"
                        v-if="joke.user_id"
                    >{{ user_name }}</a>
                    <span v-else>{{ joke.author || 'åŒ¿å' }}</span>
                </span>
                <el-link
                    type="primary"
                    class="u-copy"
                    :disabled="disabled"
                    @click="handleCopy(joke.content)"
                >
                    <i class="el-icon-document-copy"></i> å¤åˆ¶
                </el-link>

                <a
                    v-if="mode === 'single' && (isAuthor || isEditor)"
                    class="u-edit el-link el-link--primary is-underline"
                    :href="editLink('joke',joke.id)"
                    target="_blank"
                >
                    <i class="el-icon-edit-outline"></i> ç¼–è¾‘
                </a>

                <a
                    class="u-like"
                    :class="{ on:isLike}"
                    title="èµ"
                    @click="addLike"
                    v-if="isListPage"
                >
                    <i class="like-icon">{{isLike ? 'â™¥' : 'â™¡'}}</i>Like
                    <span class="like-count" v-if="count">{{ count }}</span>
                </a>

                <template v-if="isEditor">
                    <span class="el-link el-link--primary is-underline" @click="handleStar">
                        <i :class="isStar ? 'el-icon-star-off' : 'el-icon-star-on'"></i>
                        {{ isStar ? 'å–æ¶ˆç²¾é€‰' : 'è®¾ä¸ºç²¾é€‰' }}
                    </span>
                    <span
                        class="el-link el-link--primary is-underline u-delete"
                        @click="handleDelete"
                    >
                        <i class="el-icon-delete"></i> åˆ é™¤
                    </span>
                </template>
            </div>
            <div class="u-other">
                <span class="u-date">
                    <i class="el-icon-date"></i>&nbsp;
                    <time>{{ joke.created_at | dateFormat }}</time>
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import JX3_EMOTION from "@jx3box/jx3box-emotion";
import {dateFormat} from "@/utils/dateFormat";
import {
    showAvatar,
    authorLink,
    editLink,
} from "@jx3box/jx3box-common/js/utils";
import { starJoke, removeJoke, unstarJoke } from "@/service/joke";
import User from "@jx3box/jx3box-common/js/user";
import { postStat } from "@jx3box/jx3box-common/js/stat";
export default {
    name: "joke_item",
    props: ["joke", "mode"],
    data() {
        return {
            disabled: false,

            // åŠ æ˜Ÿ
            isStar: this.joke.star,

            // ç‚¹èµ
            count: 0,
            isLike: false,
        };
    },
    filters: {
        dateFormat: function (val) {
            return dateFormat(new Date(val));
        },
        showAvatar: function (val) {
            return showAvatar(val, 24);
        },
        authorLink,
    },
    computed: {
        isListPage: function () {
            return this.mode !== "single";
        },
        isEditor: function () {
            return User.isEditor();
        },
        user_avatar: function () {
            return this.joke?.user_info?.user_avatar;
        },
        user_name: function () {
            return this.joke?.user_info?.display_name;
        },
        isAuthor: function () {
            const user = User.getInfo()
            return user.uid === this.joke.user_id
        }
    },
    methods: {
        parse(str) {
            const ins = new JX3_EMOTION(str);
            return ins.code;
        },
        // å¤åˆ¶
        handleCopy(str) {
            this.disabled = true;
            navigator.clipboard.writeText(str).then(() => {
                this.copyLabel = "å·²å¤åˆ¶";

                setTimeout(() => {
                    this.copyLabel = "å¤åˆ¶";
                    this.disabled = false;
                }, 3000);
            });
        },
        // ç¼–è¾‘
        editLink,
        // ç‚¹èµ
        addLike: function () {
            if (this.isLike) return;

            this.count++;
            if (!this.isLike) {
                postStat("joke", this.joke?.id, "likes");
            }
            this.isLike = true;
        },
        // ç²¾é€‰
        handleStar() {
            if (!this.isStar) {
                starJoke(this.joke.id).then(() => {
                    this.$notify({
                        title: "æˆåŠŸ",
                        message: "åŠ ç²¾æˆåŠŸ",
                        type: "success",
                    });
                    this.isStar = true;
                    this.joke.star = true
                    this.$forceUpdate();
                    // this.$emit("update");
                });
            } else {
                this.unStar()
            }
        },
        unStar: function (){
            unstarJoke(this.joke.id).then(() => {
                this.$notify({
                    title: "æˆåŠŸ",
                    message: "å–æ¶ˆåŠ ç²¾æˆåŠŸ",
                    type: "success",
                });
                this.isStar = false;
                this.joke.star = false
                this.$forceUpdate();
            })
        },

        // åˆ é™¤
        handleDelete() {
            this.$confirm("æ­¤æ“ä½œå°†ä¼šåˆ é™¤è¯¥è¡¨æƒ…ï¼Œæ˜¯å¦ç»§ç»­ï¼Ÿ", "æç¤º", {
                confirmButtonText: "ç¡®å®š",
                cancelButtonText: "å–æ¶ˆ",
                type: "warning",
            }).then(() => {
                removeJoke(this.joke.id).then(() => {
                    this.$notify({
                        title: "æˆåŠŸ",
                        message: "åˆ é™¤æˆåŠŸ",
                        type: "success",
                    });
                    if (this.mode === "single") {
                        this.$router.go(-1);
                    } else {
                        this.$emit("update");
                    }
                });
            });
        },
        handleContent: function () {
            this.$router.push(`/joke/${this.joke.id}`);
        },
    },
};
</script>
